.formLabel {
  margin-right: 10px;
}
.contactInput {
  margin-left: 10px;
  border: 1px solid steelblue;
  border-radius: 4px;
  padding: 12px 20px;
}
.contactInput:focus {
  border: none;
  outline: 0;
  outline-offset: 0;
  box-shadow: 0 0 0 2px coral inset, 0 0 0 4px white inset;
}
.contactAddBtn {
  border-radius: 5px;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.contactFormTitle {
  margin-top: 20px;
  margin-bottom: 7px;

  text-align: left;
  padding-left: 10px;

  font-family: "Franklin Gothic Medium";
  font-size: 15px;
  color: #355681;
}
.contactBtn {
  border: 1px solid lightgray;
  border-radius: 4px;
  margin-left: 20px;
  margin-top: 20px;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  -webkit-transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
  transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
  background-color: white;
  cursor: pointer;
}
.contactBtn:hover {
  box-shadow: 0 0 40px 40px coral inset;
  color: white;
  /* transition: color 300ms ease-in-out, color 300ms ease-in-out; */
}

